import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Calendar, MapPin, Clock, Users, Download, Eye } from 'lucide-react';
import { dataAPI } from '../services/api';
import { Booking } from '../types';
import { useAuth } from '../contexts/AuthContext';

const MyBookings: React.FC = () => {
  const { user } = useAuth();
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [filter, setFilter] = useState<'all' | 'upcoming' | 'completed' | 'cancelled'>('all');

  useEffect(() => {
    if (user) {
      loadBookings();
    }
  }, [user]);

  const loadBookings = async () => {
    try {
      setLoading(true);
      setError(null);
      const bookingsData = await dataAPI.getUserBookings();
      setBookings(bookingsData);
    } catch (error: any) {
      setError('Failed to load bookings. Please try again.');
      console.error('Error loading bookings:', error);
    } finally {
      setLoading(false);
    }
  };

  const getBookingStatus = (booking: Booking) => {
    const travelDate = new Date(booking.travelDate);
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    travelDate.setHours(0, 0, 0, 0);

    if (booking.status === 'cancelled') {
      return { status: 'cancelled', color: 'text-red-600 bg-red-50', label: 'Cancelled' };
    } else if (travelDate < today) {
      return { status: 'completed', color: 'text-green-600 bg-green-50', label: 'Completed' };
    } else {
      return { status: 'upcoming', color: 'text-blue-600 bg-blue-50', label: 'Upcoming' };
    }
  };

  const filteredBookings = bookings.filter(booking => {
    if (filter === 'all') return true;
    const bookingStatus = getBookingStatus(booking);
    return bookingStatus.status === filter;
  });

  const handlePrintTicket = (booking: Booking) => {
    // In a real app, this would open a print-friendly view
    console.log('Print ticket for booking:', booking.pnr);
    alert('Print functionality would be implemented');
  };

  if (!user) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="text-center">
          <p className="text-gray-600 text-lg mb-4">Please log in to view your bookings.</p>
          <Link
            to="/login"
            className="bg-primary-600 hover:bg-primary-700 text-white px-6 py-2 rounded-md font-medium"
          >
            Login
          </Link>
        </div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
          <span className="ml-3 text-lg">Loading your bookings...</span>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-red-50 border border-red-200 rounded-md p-4">
          <p className="text-red-800">{error}</p>
          <button
            onClick={loadBookings}
            className="mt-2 bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">My Bookings</h1>
        <p className="text-gray-600 mt-2">View and manage your bus ticket bookings</p>
      </div>

      {/* Filter Tabs */}
      <div className="mb-8">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8">
            {[
              { key: 'all', label: 'All Bookings' },
              { key: 'upcoming', label: 'Upcoming' },
              { key: 'completed', label: 'Completed' },
              { key: 'cancelled', label: 'Cancelled' }
            ].map(tab => (
              <button
                key={tab.key}
                onClick={() => setFilter(tab.key as any)}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  filter === tab.key
                    ? 'border-primary-500 text-primary-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                {tab.label}
                <span className="ml-2 text-xs bg-gray-100 text-gray-600 px-2 py-1 rounded-full">
                  {tab.key === 'all' 
                    ? bookings.length 
                    : bookings.filter(b => getBookingStatus(b).status === tab.key).length
                  }
                </span>
              </button>
            ))}
          </nav>
        </div>
      </div>

      {/* Bookings List */}
      {filteredBookings.length === 0 ? (
        <div className="text-center py-12">
          <div className="mb-4">
            <Calendar className="h-16 w-16 text-gray-300 mx-auto" />
          </div>
          <h3 className="text-lg font-medium text-gray-900 mb-2">
            {filter === 'all' ? 'No bookings found' : `No ${filter} bookings`}
          </h3>
          <p className="text-gray-600 mb-6">
            {filter === 'all' 
              ? "You haven't made any bookings yet."
              : `You don't have any ${filter} bookings.`
            }
          </p>
          <Link
            to="/"
            className="bg-primary-600 hover:bg-primary-700 text-white px-6 py-2 rounded-md font-medium"
          >
            Book Your First Trip
          </Link>
        </div>
      ) : (
        <div className="space-y-6">
          {filteredBookings.map((booking) => {
            const status = getBookingStatus(booking);
            return (
              <div key={booking.id} className="bg-white rounded-lg shadow-md border border-gray-200 overflow-hidden">
                <div className="p-6">
                  <div className="flex items-start justify-between mb-4">
                    <div>
                      <div className="flex items-center space-x-2 mb-2">
                        <h3 className="text-lg font-semibold text-gray-900">
                          {booking.route?.from} → {booking.route?.to}
                        </h3>
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${status.color}`}>
                          {status.label}
                        </span>
                      </div>
                      <p className="text-sm text-gray-600">PNR: {booking.pnr}</p>
                    </div>
                    <div className="text-right">
                      <p className="text-lg font-bold text-primary-600">NPR {booking.totalAmount}</p>
                      <p className="text-sm text-gray-600">{booking.selectedSeats.length} seat(s)</p>
                    </div>
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
                    <div className="flex items-center space-x-2">
                      <Calendar className="h-4 w-4 text-gray-400" />
                      <div>
                        <p className="text-xs text-gray-600">Travel Date</p>
                        <p className="text-sm font-medium">{new Date(booking.travelDate).toLocaleDateString()}</p>
                      </div>
                    </div>

                    <div className="flex items-center space-x-2">
                      <Clock className="h-4 w-4 text-gray-400" />
                      <div>
                        <p className="text-xs text-gray-600">Departure</p>
                        <p className="text-sm font-medium">{booking.bus?.departureTime}</p>
                      </div>
                    </div>

                    <div className="flex items-center space-x-2">
                      <Users className="h-4 w-4 text-gray-400" />
                      <div>
                        <p className="text-xs text-gray-600">Passengers</p>
                        <p className="text-sm font-medium">{booking.passengers.length}</p>
                      </div>
                    </div>

                    <div className="flex items-center space-x-2">
                      <MapPin className="h-4 w-4 text-gray-400" />
                      <div>
                        <p className="text-xs text-gray-600">Seats</p>
                        <p className="text-sm font-medium">{booking.selectedSeats.join(', ')}</p>
                      </div>
                    </div>
                  </div>

                  <div className="border-t pt-4">
                    <div className="flex items-center justify-between">
                      <div className="text-sm text-gray-600">
                        <span className="font-medium">{booking.bus?.operator}</span>
                        <span className="mx-2">•</span>
                        <span>{booking.bus?.busNumber}</span>
                        <span className="mx-2">•</span>
                        <span>{booking.bus?.busType}</span>
                      </div>
                      
                      <div className="flex space-x-2">
                        <button
                          onClick={() => handlePrintTicket(booking)}
                          className="flex items-center space-x-1 text-primary-600 hover:text-primary-700 text-sm font-medium"
                        >
                          <Download className="h-4 w-4" />
                          <span>Print</span>
                        </button>
                      </div>
                    </div>
                  </div>

                  {/* Passenger Details */}
                  <div className="mt-4 pt-4 border-t bg-gray-50 -m-6 p-4">
                    <div className="flex items-center justify-between">
                      <h4 className="text-sm font-medium text-gray-900">Passengers</h4>
                      <button className="text-sm text-primary-600 hover:text-primary-700 flex items-center space-x-1">
                        <Eye className="h-4 w-4" />
                        <span>View Details</span>
                      </button>
                    </div>
                    <div className="mt-2 space-y-1">
                      {booking.passengers.slice(0, 2).map((passenger, index) => (
                        <p key={index} className="text-sm text-gray-600">
                          {passenger.name} (Age: {passenger.age}, Seat: {passenger.seatNumber})
                        </p>
                      ))}
                      {booking.passengers.length > 2 && (
                        <p className="text-sm text-gray-500">
                          +{booking.passengers.length - 2} more passengers
                        </p>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* Book Another Trip CTA */}
      {filteredBookings.length > 0 && (
        <div className="text-center mt-12 py-8 bg-gray-50 rounded-lg">
          <h3 className="text-lg font-medium text-gray-900 mb-2">
            Plan Your Next Journey
          </h3>
          <p className="text-gray-600 mb-4">
            Explore more destinations across Nepal
          </p>
          <Link
            to="/"
            className="bg-primary-600 hover:bg-primary-700 text-white px-6 py-2 rounded-md font-medium"
          >
            Book Another Trip
          </Link>
        </div>
      )}
    </div>
  );
};

export default MyBookings;
