import React, { useState, useEffect } from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
import { User, Users, ArrowLeft } from 'lucide-react';
import { busAPI } from '../services/api';
import { Bus, Seat, Passenger, SearchParams } from '../types';
import { useAuth } from '../contexts/AuthContext';

const SeatSelection: React.FC = () => {
  const { busId } = useParams<{ busId: string }>();
  const location = useLocation();
  const navigate = useNavigate();
  const { user } = useAuth();

  const [busDetails, setBusDetails] = useState<{
    busId: number;
    operator: string;
    busNumber: string;
    busType: string;
    seats: Seat[];
    basePrice: number;
  } | null>(null);
  
  const [selectedSeats, setSelectedSeats] = useState<number[]>([]);
  const [passengers, setPassengers] = useState<Passenger[]>([]);
  const [loading, setLoading] = useState(true);
  const [bookingLoading, setBookingLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const busData = location.state?.bus as Bus;
  const searchParams = location.state?.searchParams as SearchParams;

  useEffect(() => {
    if (busId) {
      loadBusSeats();
    }
  }, [busId]);

  useEffect(() => {
    // Initialize passengers when seats are selected
    if (selectedSeats.length > passengers.length) {
      const newPassengers = [...passengers];
      for (let i = passengers.length; i < selectedSeats.length; i++) {
        newPassengers.push({
          name: '',
          age: 18,
          gender: 'male',
          seatNumber: selectedSeats[i],
        });
      }
      setPassengers(newPassengers);
    } else if (selectedSeats.length < passengers.length) {
      setPassengers(passengers.slice(0, selectedSeats.length));
    }
  }, [selectedSeats]);

  const loadBusSeats = async () => {
    try {
      setLoading(true);
      const response = await busAPI.getBusSeats(parseInt(busId!));
      setBusDetails(response);
    } catch (error: any) {
      setError('Failed to load seat information');
      console.error('Error loading seats:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSeatClick = (seatNumber: number, seat: Seat) => {
    if (!seat.isAvailable) return;

    if (selectedSeats.includes(seatNumber)) {
      // Remove seat
      setSelectedSeats(selectedSeats.filter(s => s !== seatNumber));
    } else {
      // Add seat (max 6 seats)
      if (selectedSeats.length < 6) {
        setSelectedSeats([...selectedSeats, seatNumber]);
      } else {
        alert('Maximum 6 seats can be selected at once');
      }
    }
  };

  const handlePassengerChange = (index: number, field: keyof Passenger, value: string | number) => {
    const updatedPassengers = [...passengers];
    updatedPassengers[index] = {
      ...updatedPassengers[index],
      [field]: value,
    };
    setPassengers(updatedPassengers);
  };

  const calculateTotalAmount = () => {
    if (!busDetails) return 0;
    let total = selectedSeats.length * busDetails.basePrice;
    
    // Add window seat premium
    selectedSeats.forEach(seatNumber => {
      const seat = busDetails.seats.find(s => s.number === seatNumber);
      if (seat?.isWindow) {
        total += seat.price;
      }
    });
    
    return total;
  };

  const handleBookTickets = async () => {
    if (!busDetails || !searchParams) return;

    // Validate passenger details
    const invalidPassenger = passengers.find(p => !p.name.trim() || p.age < 1 || p.age > 120);
    if (invalidPassenger) {
      alert('Please fill in valid details for all passengers');
      return;
    }

    try {
      setBookingLoading(true);
      const bookingData = {
        busId: busDetails.busId,
        selectedSeats,
        passengers,
        totalAmount: calculateTotalAmount(),
        travelDate: searchParams.date,
      };

      const response = await busAPI.bookTickets(bookingData);
      navigate('/booking-confirmation', { state: { booking: response.booking } });
    } catch (error: any) {
      alert(error.response?.data?.error || 'Booking failed. Please try again.');
    } finally {
      setBookingLoading(false);
    }
  };

  const getSeatColor = (seat: Seat) => {
    if (!seat.isAvailable) return 'bg-gray-400 cursor-not-allowed';
    if (selectedSeats.includes(seat.number)) return 'bg-primary-600 text-white';
    if (seat.isWindow) return 'bg-green-200 hover:bg-green-300';
    return 'bg-gray-200 hover:bg-gray-300';
  };

  const renderSeatMap = () => {
    if (!busDetails) return null;

    const rows = Math.ceil(busDetails.seats.length / 4);
    const seatMap = [];

    for (let row = 0; row < rows; row++) {
      const rowSeats = [];
      for (let col = 0; col < 4; col++) {
        const seatIndex = row * 4 + col;
        if (seatIndex < busDetails.seats.length) {
          const seat = busDetails.seats[seatIndex];
          rowSeats.push(
            <button
              key={seat.number}
              onClick={() => handleSeatClick(seat.number, seat)}
              disabled={!seat.isAvailable}
              className={`
                w-12 h-12 rounded text-xs font-medium border-2 border-gray-300 transition-colors
                ${getSeatColor(seat)}
                ${col === 1 ? 'mr-8' : ''}
              `}
              title={`Seat ${seat.number} ${seat.isWindow ? '(Window)' : ''} ${seat.isAvailable ? '' : '(Occupied)'}`}
            >
              {seat.number}
            </button>
          );
        }
      }
      seatMap.push(
        <div key={row} className="flex justify-center space-x-2 mb-2">
          {rowSeats}
        </div>
      );
    }

    return seatMap;
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
          <span className="ml-3 text-lg">Loading seat map...</span>
        </div>
      </div>
    );
  }

  if (error || !busDetails) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-red-50 border border-red-200 rounded-md p-4">
          <p className="text-red-800">{error || 'Failed to load bus information'}</p>
          <button
            onClick={() => navigate(-1)}
            className="mt-2 bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700"
          >
            Go Back
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="flex items-center mb-6">
        <button
          onClick={() => navigate(-1)}
          className="flex items-center text-primary-600 hover:text-primary-700 mr-4"
        >
          <ArrowLeft className="h-4 w-4 mr-1" />
          Back
        </button>
        <h1 className="text-2xl font-bold text-gray-900">
          Select Seats - {busDetails.operator}
        </h1>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Seat Map */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <div className="mb-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Choose Your Seats</h2>
            
            {/* Bus Info */}
            <div className="bg-gray-50 p-4 rounded-lg mb-4">
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-gray-600">Bus:</span>
                  <span className="ml-2 font-medium">{busDetails.busNumber}</span>
                </div>
                <div>
                  <span className="text-gray-600">Type:</span>
                  <span className="ml-2 font-medium">{busDetails.busType}</span>
                </div>
                {busData && (
                  <>
                    <div>
                      <span className="text-gray-600">Route:</span>
                      <span className="ml-2 font-medium">{busData.route?.from} to {busData.route?.to}</span>
                    </div>
                    <div>
                      <span className="text-gray-600">Date:</span>
                      <span className="ml-2 font-medium">{searchParams ? new Date(searchParams.date).toLocaleDateString() : ''}</span>
                    </div>
                  </>
                )}
              </div>
            </div>

            {/* Legend */}
            <div className="flex items-center space-x-6 mb-6 text-sm">
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 bg-gray-200 rounded border-2 border-gray-300"></div>
                <span>Available</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 bg-green-200 rounded border-2 border-gray-300"></div>
                <span>Window</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 bg-primary-600 rounded border-2 border-gray-300"></div>
                <span>Selected</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 bg-gray-400 rounded border-2 border-gray-300"></div>
                <span>Occupied</span>
              </div>
            </div>
          </div>

          {/* Seat Layout */}
          <div className="border-2 border-gray-300 rounded-lg p-6 bg-gray-50">
            <div className="text-center mb-4">
              <div className="w-16 h-4 bg-gray-400 rounded-full mx-auto mb-2"></div>
              <span className="text-xs text-gray-600">Driver</span>
            </div>
            {renderSeatMap()}
          </div>

          <div className="mt-4 text-center text-sm text-gray-600">
            Selected Seats: {selectedSeats.length > 0 ? selectedSeats.join(', ') : 'None'}
          </div>
        </div>

        {/* Passenger Details & Booking */}
        <div className="space-y-6">
          {/* Passenger Details */}
          {selectedSeats.length > 0 && (
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                <Users className="h-5 w-5 mr-2" />
                Passenger Details
              </h2>
              
              <div className="space-y-4">
                {passengers.map((passenger, index) => (
                  <div key={index} className="border border-gray-200 rounded-lg p-4">
                    <div className="flex items-center justify-between mb-3">
                      <h3 className="font-medium text-gray-900">
                        Passenger {index + 1} (Seat {selectedSeats[index]})
                      </h3>
                      <User className="h-4 w-4 text-gray-400" />
                    </div>
                    
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          Full Name *
                        </label>
                        <input
                          type="text"
                          value={passenger.name}
                          onChange={(e) => handlePassengerChange(index, 'name', e.target.value)}
                          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                          placeholder="Enter full name"
                          required
                        />
                      </div>
                      
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          Age *
                        </label>
                        <input
                          type="number"
                          value={passenger.age}
                          onChange={(e) => handlePassengerChange(index, 'age', parseInt(e.target.value) || 0)}
                          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                          min="1"
                          max="120"
                          required
                        />
                      </div>
                      
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          Gender *
                        </label>
                        <select
                          value={passenger.gender}
                          onChange={(e) => handlePassengerChange(index, 'gender', e.target.value as 'male' | 'female' | 'other')}
                          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                          required
                        >
                          <option value="male">Male</option>
                          <option value="female">Female</option>
                          <option value="other">Other</option>
                        </select>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Booking Summary */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Booking Summary</h2>
            
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-gray-600">Selected Seats ({selectedSeats.length})</span>
                <span className="font-medium">NPR {selectedSeats.length * busDetails.basePrice}</span>
              </div>
              
              {selectedSeats.some(seatNumber => {
                const seat = busDetails.seats.find(s => s.number === seatNumber);
                return seat?.isWindow;
              }) && (
                <div className="flex justify-between">
                  <span className="text-gray-600">Window Seat Premium</span>
                  <span className="font-medium">
                    NPR {selectedSeats.reduce((total, seatNumber) => {
                      const seat = busDetails.seats.find(s => s.number === seatNumber);
                      return total + (seat?.isWindow ? seat.price : 0);
                    }, 0)}
                  </span>
                </div>
              )}
              
              <hr className="border-gray-200" />
              
              <div className="flex justify-between text-lg font-semibold">
                <span>Total Amount</span>
                <span className="text-primary-600">NPR {calculateTotalAmount()}</span>
              </div>
            </div>

            <button
              onClick={handleBookTickets}
              disabled={selectedSeats.length === 0 || bookingLoading || passengers.some(p => !p.name.trim())}
              className="w-full mt-6 bg-primary-600 hover:bg-primary-700 disabled:bg-gray-400 text-white px-6 py-3 rounded-md font-medium transition-colors"
            >
              {bookingLoading ? 'Processing...' : `Book ${selectedSeats.length} Seat${selectedSeats.length !== 1 ? 's' : ''}`}
            </button>
            
            {selectedSeats.length === 0 && (
              <p className="text-sm text-gray-500 text-center mt-2">
                Please select at least one seat to continue
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SeatSelection;
