import axios from 'axios';
import { Bus, Booking, SearchParams, PopularRoute, Route, Seat, Passenger } from '../types';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

const api = axios.create({
  baseURL: `${API_BASE_URL}/api`,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor to add auth token
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Response interceptor to handle auth errors
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

export const authAPI = {
  login: async (email: string, password: string) => {
    const response = await api.post('/login', { email, password });
    return response.data;
  },

  register: async (userData: {
    name: string;
    email: string;
    phone: string;
    password: string;
  }) => {
    const response = await api.post('/register', userData);
    return response.data;
  },
};

export const busAPI = {
  searchBuses: async (params: SearchParams): Promise<{ buses: Bus[] }> => {
    const response = await api.get('/search', { params });
    return response.data;
  },

  getBusSeats: async (busId: number): Promise<{
    busId: number;
    operator: string;
    busNumber: string;
    busType: string;
    seats: Seat[];
    basePrice: number;
  }> => {
    const response = await api.get(`/bus/${busId}/seats`);
    return response.data;
  },

  bookTickets: async (bookingData: {
    busId: number;
    selectedSeats: number[];
    passengers: Passenger[];
    totalAmount: number;
    travelDate: string;
  }) => {
    const response = await api.post('/book', bookingData);
    return response.data;
  },
};

export const dataAPI = {
  getRoutes: async (): Promise<Route[]> => {
    const response = await api.get('/routes');
    return response.data;
  },

  getPopularRoutes: async (): Promise<PopularRoute[]> => {
    const response = await api.get('/popular-routes');
    return response.data;
  },

  getUserBookings: async (): Promise<Booking[]> => {
    const response = await api.get('/bookings');
    return response.data;
  },
};

export default api;
